

























import { Component, Vue } from 'vue-property-decorator';

@Component({
  components: {},
})
export default class extends Vue {
  isActive = true;
  menu_icon: 'times' | 'list' = 'list';
  isOpen = false;

  open_menu() {
    this.menu_icon = this.menu_icon === 'list' ? 'times' : 'list';
    this.isOpen = !this.isOpen;
  }
}
